import React, { useState } from 'react'
import PropTypes from 'prop-types'


const Blog = ({ user, blog, updateLikes, removeBlog }) => {
  const [viewAll, setView] = useState(false)

  Blog.propTypes = {
    user: PropTypes.string.isRequired,
    blog: PropTypes.object.isRequired,
    updateLikes: PropTypes.func.isRequired,
    removeBlog: PropTypes.func.isRequired
  }

  const blogStyle = {
    paddingTop: 10,
    paddingLeft: 2,
    border: 'solid Violet',
    borderWidth: 1,
    marginBottom: 5
  }

  return (
    <div style={blogStyle}>
      {viewAll === false
        ? (<div> {blog.title}
          <b> {blog.author}</b>
          <button type="button" onClick={() => setView(true)}>view</button></div>)
        : (<div> {blog.title}
          <b> {blog.author}</b>
          <button type="button" onClick={() => setView(false)}>hide</button>
          <div>
            <u>{blog.url}</u>
            <br></br>
    likes {blog.likes}
            <button type="button" onClick={updateLikes}>like</button>
            <br></br>
            {blog.user.name}
          </div>
          {user.name === blog.user.name
            ? (<div>
              <button className={'remove_button'} type="button" onClick={removeBlog}>remove</button>
            </div>)
            : (<div></div>)}
        </div>)}
    </div>
  )
}

export default Blog